import { Component, OnInit } from '@angular/core';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-about-us',
  templateUrl: './about-us.component.html',
  styleUrls: ['./about-us.component.css']
})
export class AboutUsComponent implements OnInit {

  settings: Settings;
  aboutUs: any;
  constructor(private homeService: HomePageServicesService, private sanitizer: DomSanitizer) { }

  ngOnInit() {
    this.getContactDetails();
  }
  async getContactDetails() {
    this.settings = await this.homeService.getSettings();
    this.aboutUs = this.sanitizer.bypassSecurityTrustHtml(this.settings.infoDaTa.aboutUs);
  }

}
