import { Component, OnInit } from '@angular/core';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';

@Component({
  selector: 'app-programs',
  templateUrl: './programs.component.html',
  styleUrls: ['./programs.component.css']
})
export class ProgramsComponent implements OnInit {

  programs: Array<Program>;
  programsCount: number;
  specialBanners: Array<SpecialBanner>;
  constructor(private programsService: ProgramsService, public homeService: HomePageServicesService) {
    this.programsCount = 6;
  }

  ngOnInit() {
    this.loadPrograms();
  }
  async loadPrograms() {
    this.programs = await this.programsService.getProgramsList(this.programsCount);
    this.specialBanners = await this.homeService.getSpecialBanners();
    //this.programs = this.programs.filter((program) => program.isShowen);
  }
  async loadMore() {
    this.programsCount = this.programsCount + 6;
    this.loadPrograms();
  }

}
