import { Component, OnInit } from '@angular/core';
import { Constants } from 'src/app/Constants';
import { ServiceHandlerProvider } from 'src/app/services/service-handler.service';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { ArticlesService } from 'src/app/services/uiServices/articles.service';
import * as moment from "moment";
import { ScheduleService } from 'src/app/services/uiServices/schedule.service';
import { Router } from "@angular/router";

declare var $: any;

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {
  settings: Settings;
  liveUrl: any;
  videosUrl: string;
  programYoutubeVideos: Array<any>;
  bannerVideo: string;
  articles: Array<Article>;
  sportsArticles: Array<Article>;
  newsProgram: Program;
  specialPrograms: Array<SpecialPrograms>;
  specialBanners: Array<SpecialBanner>;
  program1YoutubeVideos: Array<any>;
  program2YoutubeVideos: Array<any>;
  program3YoutubeVideos: Array<any>;
  program4YoutubeVideos: Array<any>;
  program5YoutubeVideos: Array<any>;
  program6YoutubeVideos: Array<any>;
  currentPrograms: Array<Schedule> = [];
  currentVideoId: string;
  newsProgramVideo: any;
  constructor(
    private homeService: HomePageServicesService,
    private serviceHandeler: ServiceHandlerProvider,
    private articlesService: ArticlesService,
    private scheduleService: ScheduleService,
    private router: Router,
  ) { }

  async ngOnInit() {
    this.getCurrentAndUpcoming();
    await this.getContactDetails();
    await this.loadArticles();
  }
  initView() {
    $(document).ready(function () {
      $('.navbar-nav li').click(function () {
        $(this).siblings('li').removeClass('active');
        $(this).addClass('active');
      });

      if ($(window).width() > 770) {
        $('header #demo').css('height', (($(window).width()) / 2) - 80);
      }

      else {
        $('header #demo').css('height', (($(window).width()) / 2));

      }
      $('.rio-promos').slick({
        dots: false,
        infinite: false,
        speed: 1200,
        slidesToShow: 5,
        slidesToScroll: 5,
        autoplay: false,
        autoplaySpeed: 2000,
        arrows: true,
        loop: false,
        responsive: [
          {
            breakpoint: 922,
            settings: {
              arrows: true,
              slidesToShow: 3,
              slidesToScroll: 1,
              speed: 900,
            }
          },
          {
            breakpoint: 700,
            settings: {
              arrows: true,
              slidesToShow: 2,
              slidesToScroll: 1,
              speed: 900,
            }
          },
          {
            breakpoint: 600,
            settings: {
              arrows: true,
              slidesToShow: 1,
              slidesToScroll: 1,
              speed: 900,
            }
          }]
      });


      // $(window).on("scroll", function () {
      //   if ($(window).scrollTop() <= ($(".footer").offset().top) - 500) {
      //     $(".navbar").fadeIn(700, "linear");
      //   }
      //   else {

      //     $(".navbar").fadeOut(700, "linear");
      //   }



      // });

    });


    $(window).on("resize", function () {
      if ($(window).width() > 770) {
        $('header #demo').css('height', (($(window).width()) / 2) - 80);
      }

      else {
        $('header #demo').css('height', (($(window).width()) / 2));

      }

      //        $('header #demo').css('height', 'l' ); 
      //         $('.centered-div').css('left',($(window).width() - $('.centered-div').width())/2); 

    });
  }
  async getContactDetails() {
    this.settings = await this.homeService.getSettings();
    // console.log(this.settings.liveUrl);
    this.videosUrl = this.settings.infoDaTa.videosUrl;
    this.newsProgram = this.settings.newsPrograms;
    // console.log("News Program-------------");
    // console.log(this.newsProgram);
    this.specialBanners = await this.homeService.getSpecialBanners();
    this.specialPrograms = await this.homeService.getSpecialPrograms();
    this.specialPrograms = this.specialPrograms.filter((program) => program.program != null && program.program != undefined);
    let params = { "part": "snippet,contentDetails", "maxResults": "5", "playlistId": this.videosUrl, "key": Constants.YOUTUBE_API_KEY };
    this.serviceHandeler.runService("https://www.googleapis.com/youtube/v3/playlistItems", "GET", null, null, null, params).subscribe(response => {
      // console.log(response);
      this.programYoutubeVideos = response.items;
      this.bannerVideo = this.programYoutubeVideos[0];
      // console.log("Last item");
      // console.log(this.bannerVideo);
    }, error => {
      // console.log(error);
    });
    let params2 = { "part": "snippet,contentDetails", "maxResults": "1", "playlistId": this.newsProgram.playlistUrl, "key": Constants.YOUTUBE_API_KEY };
    this.serviceHandeler.runService("https://www.googleapis.com/youtube/v3/playlistItems", "GET", null, null, null, params2).subscribe(response => {
      // console.log(response);
      this.newsProgramVideo = response.items[0];
      // console.log(this.newsProgramVideo);
    }, error => {
      // console.log(error);
    });

    if (this.specialPrograms[0]) {
      await this.getProgramsVideos(this.specialPrograms[0].program.playlistUrl, 1);
    }
    if (this.specialPrograms[1]) {
      await this.getProgramsVideos(this.specialPrograms[1].program.playlistUrl, 2);
    }
    if (this.specialPrograms[2]) {
      await this.getProgramsVideos(this.specialPrograms[2].program.playlistUrl, 3);
    }
    if (this.specialPrograms[3]) {
      await this.getProgramsVideos(this.specialPrograms[3].program.playlistUrl, 4);
    }
    if (this.specialPrograms[4]) {
      await this.getProgramsVideos(this.specialPrograms[4].program.playlistUrl, 5);
    }
    if (this.specialPrograms[5]) {
      await this.getProgramsVideos(this.specialPrograms[5].program.playlistUrl, 6);
    }


  }
  liveStreamClicked() {
    // console.log("Live stream clicked");
  }
  async loadArticles() {
    this.sportsArticles = await this.articlesService.getArticlesList(true);
    this.sportsArticles = this.sportsArticles.filter((article) => article.isShowen);
    this.articles = await this.articlesService.getArticlesList();
    this.articles = this.articles.filter((article) => article.isShowen);
    await this.delay(1000);
    this.initView();
  }
  getPublishTime(time: string) {
    return time ? time.substr(time.indexOf("T") + 1, 5) : "";
  }

  async getProgramsVideos(programPlayListId: string, programNum: number) {
    let params = { "part": "snippet,contentDetails", "maxResults": "10", "playlistId": programPlayListId, "key": Constants.YOUTUBE_API_KEY, "h1": "ar" };
    this.serviceHandeler.runService("https://www.googleapis.com/youtube/v3/playlistItems", "GET", null, null, null, params).subscribe(response => {
      // console.log(response);
      switch (programNum) {
        case 1:
          this.program1YoutubeVideos = response.items;
          break;
        case 2:
          this.program2YoutubeVideos = response.items;
          break;
        case 3:
          this.program3YoutubeVideos = response.items;
          break;
        case 4:
          this.program4YoutubeVideos = response.items;
          break;
        case 5:
          this.program5YoutubeVideos = response.items;
          break;
        case 6:
          this.program6YoutubeVideos = response.items;
          break;
      }


    }, error => {
      // console.log(error);
    });
  }
  async getCurrentAndUpcoming() {
    moment.locale('en');
    let today = moment().toISOString(true);
    // console.log(today);
    this.currentPrograms = await this.scheduleService.getCurrentAndUpcoming(moment().toString().substr(0, 3), this.getPublishTime(today), moment(today).format("YYYY-MM-DD"));
    // console.log("Current schedule");
    // console.log(this.currentPrograms);
    this.currentPrograms = this.currentPrograms.filter(item => {
      if (item.program) {
        return item.program.isShowen
      }
      else if (item.event) {
        return item.event.isShowen
      }
      else {
        return false;
      }
    });
    // console.log("Current schedule");
    // console.log(this.currentPrograms);
  }
  navigateToProgram(schedule: Schedule | SpecialBanner) {
    if (schedule.program) {
      this.router.navigateByUrl("/program/" + schedule.program.id);
    }
  }
  onVidoeSelected(videoId: string) {
    this.currentVideoId = videoId;
  }
  async changeBannerVideo(item: any) {
    this.bannerVideo = null;
    await this.delay(500);
    this.bannerVideo = item;
  }
  async delay(ms: number) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }
  translateDate(date: string): string {
    moment.locale('ar');
    return moment(date).format("YYYY/MM/DD");
  }

}
