import { Injectable } from '@angular/core';
import { ServiceHandlerProvider } from '../service-handler.service';
import { Constants } from 'src/app/Constants';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ScheduleService {

  constructor(private serviceHadler: ServiceHandlerProvider) { }

  async getDaySchedules(dayName: string, dayDate: string): Promise<Array<Schedule>> {
    // console.log(dayName + "---" + dayDate);
    return new Promise<Array<Schedule>>((resolve, reject) => {
      let query = `query {
        daySchedules(day:"${dayName}",date:"${dayDate}"){
          id
          timeDate
          type
          program{
            id,
            name,
            icon,
            isShowen,
            mobBanner
          }
          event{
            id,
            name,
            icon,
            isShowen
          }
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.daySchedules) {
          let daySchedules: Array<Schedule> = response.data.daySchedules;
          daySchedules = daySchedules.filter(item => {
            if (item.program) {
              return item.program.isShowen
            }
            else if (item.event) {
              return item.event.isShowen
            }
            else {
              return false;
            }
          });
          resolve(daySchedules);
        }
        else {
          reject("Failed to load daySchedules");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async getCurrentAndUpcoming(dayName: string, time: string, dayDate: string): Promise<Array<Schedule>> {
    return new Promise<Array<Schedule>>((resolve, reject) => {
      let query = `query{
        currentSchedule(day:"${dayName}", time:"1970-01-01T${time}:00.000Z",date:"${dayDate}"){
           id
           timeDate
           finishTimeDate
           startDate
           endDate
           event{
             id
             name,
             icon,
             isShowen
           }
           program{
             id
             name,
             icon,
             mobBanner,
             isShowen
           }
         }
       }`;
      // console.log(query);
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.currentSchedule) {
          resolve(response.data.currentSchedule);
        }
        else {
          reject("Failed to load daySchedules");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
}
