import { Component, OnInit } from '@angular/core';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';

@Component({
  selector: 'app-team',
  templateUrl: './team.component.html',
  styleUrls: ['./team.component.css']
})
export class TeamComponent implements OnInit {

  team: Array<Presenter>;
  constructor(private homeService: HomePageServicesService) { }

  async ngOnInit() {
    this.team = await this.homeService.getTeam();

  }

}
