import { Injectable } from '@angular/core';
import { ServiceHandlerProvider } from '../service-handler.service';
import { Constants } from 'src/app/Constants';

@Injectable({
  providedIn: 'root'
})
export class ArticlesService {

  constructor(private serviceHadler: ServiceHandlerProvider) { }

  async getArticlesList(isSport?: boolean, count?: number): Promise<Array<Article>> {
    return new Promise<Array<Article>>((resolve, reject) => {
      let query = isSport ? `query{
        sportsArticles(first:${count ? count : 50},where:{isShowen:true},orderBy:publishDate_DESC){
          id,
          name,
          writer,
          caption,
          publishDate,
          publishTime,
          article,
          images,
          videos,
          isShowen
        }
      }`: `query{
        articles(first:${count ? count : 50},where:{isShowen:true},orderBy:publishDate_DESC){
          id,
          name,
          writer,
          caption,
          publishDate,
          publishTime,
          article,
          images,
          videos,
          isShowen
        }
      }`;

      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);

        if (response.data && response.data.sportsArticles) {
          resolve(response.data.sportsArticles);
        }
        else if (response.data && response.data.articles) {
          resolve(response.data.articles);
        }
        else {
          reject("Failed to load articles");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async deleteArticle(articleId: string, isSport?: boolean): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = isSport ? `mutation{
        deleteSportsArticle(where:{id:"${articleId}"}){
         id,
         name
       }
       }` : `mutation{
        deleteArticle(where:{id:"${articleId}"}){
         id,
         name
       }
       }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.deleteSportsArticle) {
          resolve(true);
        }
        else if (response.data && response.data.deleteArticle) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async getArticleDetails(articleId: string, isSport?: boolean): Promise<Article> {
    return new Promise<Article>((resolve, reject) => {
      let query = isSport ? `query{
        sportsArticle(where:{id:"${articleId}"}){
          id,
          name,
          writer,
          caption,
          publishDate,
          publishTime,
          article,
          images,
          videos,
          isShowen
        }
      }`: `query{
        article(where:{id:"${articleId}"}){
          id,
          name,
          writer,
          caption,
          publishDate,
          publishTime,
          article,
          images,
          videos,
          isShowen
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.sportsArticle) {
          resolve(response.data.sportsArticle);
        }
        else if (response.data && response.data.article) {
          resolve(response.data.article);
        }
        else {
          reject("Failed to load article");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async createArticle(article: Article, isSport?: boolean): Promise<boolean> {
    // console.log(article);
    return new Promise<boolean>((resolve, reject) => {
      let queryKey = isSport ? "createSportsArticle" : "createArticle";
      let query = `mutation{
        ${queryKey}(data:{
          name:"${article.name}",
          writer:"${article.writer}",
          caption:"${article.caption}",
          publishDate:"${article.publishDate}",
          publishTime:"${article.publishTime}",
          article:"${this.escapeDoubleQuotes(article.article)}",
          videos:"${article.videos}",
          isShowen:false,
          images:{
            set:[
              "${article.images[0]}"
            ]
          },
          
        }){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.createSportsArticle) {
          resolve(true);
        }
        else if (response.data && response.data.createArticle) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async updatedArticle(article: Article, isSport?: boolean): Promise<boolean> {
    // console.log(article);
    return new Promise<boolean>((resolve, reject) => {
      let queryKey = isSport ? "updateSportsArticle" : "updateArticle";
      let query = `mutation{
        ${queryKey}(
          where:{id:"${article.id}"},
          data:{
          name:"${article.name}",
          writer:"${article.writer}",
          caption:"${article.caption}",
          publishDate:"${article.publishDate}",
          publishTime:"${article.publishTime}",
          article:"${this.escapeDoubleQuotes(article.article)}",
          videos:"${article.videos}",
          isShowen:${article.isShowen},
          images:{
            set:[
              "${article.images[0]}"
            ]
          },
          
        }){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.updateSportsArticle) {
          resolve(true);
        }
        else if (response.data && response.data.updateArticle) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async changeArticleAppearanceStatus(articleId: string, status: boolean, isSport?: boolean): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let queryKey = isSport ? "updateSportsArticle" : "updateArticle";
      let query = `mutation{
        ${queryKey}(
          where:{id:"${articleId}"},
          data:{isShowen: ${status}}
        ){
          id,
          name,
          isShowen
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.updateSportsArticle) {
          resolve(true);
        }
        else if (response.data && response.data.updateArticle) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  escapeDoubleQuotes(str) {
    return str.replace(/\\([\s\S])|(")/g, "\\$1$2"); // thanks @slevithan!
  }
}
