  async updateSpecialBanners(settingsId: string, firstBannerId: string, secondBannerId: string, thirdBannerId: string, fourthBannerId: string, fivethBannerId: string, sixthBannerId: string, priviousBanners: Array<Program>) {

    let disconnectQuery = "[]";
    switch (priviousBanners.length) {
      case 0:
        break;
      case 1:
        disconnectQuery = `[{id:"${priviousBanners[0].id}"}]`;
        break;
      case 2:
        disconnectQuery = `[{id:"${priviousBanners[0].id}"}, {id:"${priviousBanners[1].id}"}]`;
        break;
      case 3:
        disconnectQuery = `[{id:"${priviousBanners[0].id}"}, {id:"${priviousBanners[1].id}"}, {id:"${priviousBanners[2].id}"}]`;
        break;
      case 4:
        disconnectQuery = `[{id:"${priviousBanners[0].id}"}, {id:"${priviousBanners[1].id}"}, {id:"${priviousBanners[2].id}"}, {id:"${priviousBanners[3].id}"}]`;
        break;
      case 5:
        disconnectQuery = `[{id:"${priviousBanners[0].id}"}, {id:"${priviousBanners[1].id}"}, {id:"${priviousBanners[2].id}"}, {id:"${priviousBanners[3].id}"}, {id:"${priviousBanners[4].id}"}]`;
        break;
      case 6:
        disconnectQuery = `[{id:"${priviousBanners[0].id}"}, {id:"${priviousBanners[1].id}"}, {id:"${priviousBanners[2].id}"}, {id:"${priviousBanners[3].id}"}, {id:"${priviousBanners[4].id}"},{id:"${priviousBanners[5].id}"}]`;
        break;
    }
    let query = `mutation{
      updateSetting(where:{id:"${settingsId}"},data:{
        specialBanners:{
          disconnect:${disconnectQuery}
        }
      }){
        specialBanners{
          id,name
        }
      }
    }`;
    return new Promise<boolean>((resolve, reject) => {
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.updateSetting) {
          let query2 = `mutation{
            updateSetting(where:{id:"${settingsId}"},data:{
              specialBanners:{
                connect:[{id:"${firstBannerId}"},{id:"${secondBannerId}"},{id:"${thirdBannerId}"},{id:"${fourthBannerId}"},{id:"${fivethBannerId}"},{id:"${sixthBannerId}"}]
              }
            }){
              specialBanners{
                id,name
              }
            }
          }`;
          this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query2 }).subscribe((response) => {
            // console.log(response);
            if (response.data && response.data.updateSetting) {
              resolve(true);
            }
            else {
              reject("Please check your internet connection");
            }
          }, (error) => {
            // console.log(error);
            reject("Please check your internet connection");
          });
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });


  }
