  async searchArticles(searchKeyword: string): Promise<Array<Article>> {
    return new Promise<Array<Article>>((resolve, reject) => {
      let query = `query{
        search(keyword:"${searchKeyword}"){
          id,
          name,
          writer,
          caption,
          publishDate,
          publishTime,
          article,
          images,
          videos,
          isShowen,
          type
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.search) {
          resolve(response.data.search);
        }
        else {
          reject("Failed to search");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  escapeDoubleQuotes(str) {
    return str.replace(/\\([\s\S])|(")/g, "\\$1$2"); // thanks @slevithan!
  }
