import { Component, OnInit } from '@angular/core';
import { NavigationService } from 'src/app/services/navigation.service';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {

  email: string;
  phone: string;
  address: string;
  settings: Settings;
  showData: boolean = false;
  constructor(private navigationService: NavigationService, private homeService: HomePageServicesService) { }

  ngOnInit() {
    this.navigationService.setNavigationLink = "contactUs";
    this.getContactDetails();
  }
  async getContactDetails() {
    this.settings = await this.homeService.getSettings();
    if (this.settings) {
      this.showData = true;
      this.email = this.settings.infoDaTa.email;
      this.phone = this.settings.infoDaTa.phone;
      this.address = this.settings.infoDaTa.address;
    }


  }

}
