import { Component, OnInit } from '@angular/core';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';

@Component({
  selector: 'app-mobile-apps-icons',
  templateUrl: './mobile-apps-icons.component.html',
  styleUrls: ['./mobile-apps-icons.component.css']
})
export class MobileAppsIconsComponent implements OnInit {

  androidUrl: string;
  iosUrl: string;
  settings: Settings;
  constructor(private homeService: HomePageServicesService) { }

  async ngOnInit() {
    this.settings = await this.homeService.getSettings();
    this.androidUrl = this.settings.infoDaTa.andUrl;
    this.iosUrl = this.settings.infoDaTa.iosUrl;
  }
  onNavigate(url: string) {
    window.open(url.startsWith("http") ? url : "https://" + url, "_blank");
  }
}
