import { Component, OnInit } from '@angular/core';
import { ProgramsService } from 'src/app/services/uiServices/programs.service';
import { ActivatedRoute } from '@angular/router';
import { ServiceHandlerProvider } from 'src/app/services/service-handler.service';
import { Constants } from 'src/app/Constants';
import { Router } from '@angular/router';
import * as moment from "moment";
import { MetaService } from '@ngx-meta/core';

@Component({
  selector: 'app-program-details',
  templateUrl: './program-details.component.html',
  styleUrls: ['./program-details.component.css']
})
export class ProgramDetailsComponent implements OnInit {
  selectedProgram: Program = {
    name: "",
    description: "",
    duration: "",
    programDate: "",
    icon: "",
    mobBanner: "",
    playlistUrl: "",
    webBanner: "",
    isShowen: false,
    presenter: ""
  };
  sharingLink: string = "";
  programYoutubeVideos: Array<any>;
  videosCount: number;

  programId: string;
  constructor(
    private programService: ProgramsService,
    private route: ActivatedRoute,
    private serviceHandeler: ServiceHandlerProvider,
    private router: Router,
    private readonly meta: MetaService) {
    this.videosCount = 9;
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.programId = params.id;
      // console.log(this.programId);
      this.getProgramDetails();
    });
    this.sharingLink = Constants.SHARING_URL + this.router.url;
  }
  async getProgramDetails() {
    this.selectedProgram = await this.programService.getProgramsDetails(this.programId);

    this.meta.setTag('og:title', this.selectedProgram.name);
    this.meta.setTag('og:description', this.selectedProgram.description);
    this.meta.setTag('og:image', this.selectedProgram.mobBanner);
    this.meta.setTag('twitter:title', this.selectedProgram.name);
    this.meta.setTag('twitter:description', this.selectedProgram.description);
    this.meta.setTag('og:image', this.selectedProgram.mobBanner);
    this.getVideos();
  }
  getVideos() {
    let params = { "part": "snippet,contentDetails", "maxResults": this.videosCount, "playlistId": this.selectedProgram.playlistUrl, "key": Constants.YOUTUBE_API_KEY };
    this.serviceHandeler.runService("https://www.googleapis.com/youtube/v3/playlistItems", "GET", null, null, null, params).subscribe(response => {
      // console.log(response);
      this.programYoutubeVideos = response.items;
    }, error => {
      // console.log(error);
    })
  }
  async loadMore() {
    this.videosCount = this.videosCount + 5;
    this.getVideos();
  }
  translateDate(date: string): string {
    moment.locale('ar');
    return moment(date).format("YYYY/MM/DD");
  }
}
