import { Component, OnInit } from '@angular/core';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-team-detail',
  templateUrl: './team-detail.component.html',
  styleUrls: ['./team-detail.component.css']
})
export class TeamDetailComponent implements OnInit {

  team: Array<Presenter>;
  selectedMemberId: string;
  selectedMember: Presenter;
  constructor(private homeService: HomePageServicesService, private route: ActivatedRoute, ) { }

  async ngOnInit() {
    this.route.params.subscribe(params => {
      this.selectedMemberId = params.id;
      this.getTeam();
    });

  }
  async getTeam() {
    this.team = await this.homeService.getTeam();
    this.selectedMember = this.team.find(member => member.id == this.selectedMemberId);
  }

}
