import { Injectable } from '@angular/core';
import { ServiceHandlerProvider } from '../service-handler.service';
import { Constants } from 'src/app/Constants';

@Injectable({
  providedIn: 'root'
})
export class ProgramsService {

  constructor(private serviceHadler: ServiceHandlerProvider) {
  }
  async getProgramsList(count?: number): Promise<Array<Program>> {
    return new Promise<Array<Program>>((resolve, reject) => {
      let query = `query{
        programs(first:${count ? count : 50},where:{isShowen:true}){
          id,
          name,
          icon,
          isShowen,
          webBanner
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.programs) {
          resolve(response.data.programs);
        }
        else {
          reject("Failed to load programs");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async getProgramsDetails(programId: string): Promise<Program> {
    return new Promise<Program>((resolve, reject) => {
      let query = `query{
        program(where:{id:"${programId}"}){
          id,
          name,
          programDate,
          description,
          duration,
          webBanner,
          mobBanner,
          icon,
          playlistUrl,
          isShowen,
          presenter
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.program) {
          resolve(response.data.program);
        }
        else {
          reject("Failed to load programs");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async changeProgramAppearanceStatus(programId: string, status: boolean): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        updateProgram(
          where:{id:"${programId}"},
          data:{isShowen: ${status}}
        ){
          id,
          name,
          isShowen
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.updateProgram) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async deleteProgram(programId: string): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        deleteProgram(where:{id:"${programId}"}){
         id,
         name
       }
       }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.deleteProgram) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async createProgram(program: Program): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        createProgram(data:{
          name:"${program.name}",
          programDate:"${program.name}",
          description:"${program.description}",
          presenter:"${program.presenter}",
          duration: ${parseInt(program.duration)},
          webBanner:"${program.webBanner}",
          mobBanner:"${program.mobBanner}",
          icon:"${program.icon}",
          playlistUrl:"${program.playlistUrl}",
          isShowen:false
        }){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.createProgram) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
  async updateProgram(program: Program): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      let query = `mutation{
        updateProgram(
          where:{id:"${program.id}"},
          data:{
          name:"${program.name}",
          programDate:"${program.name}",
          description:"${program.description}",
          presenter:"${program.presenter}",
          duration: ${parseInt(program.duration)},
          webBanner:"${program.webBanner}",
          mobBanner:"${program.mobBanner}",
          icon:"${program.icon}",
          playlistUrl:"${program.playlistUrl}",
          isShowen: ${program.isShowen}
        }){
          id
        }
      }`;
      this.serviceHadler.runService(Constants.BASE_URL, "POST", null, { "query": query }).subscribe((response) => {
        // console.log(response);
        if (response.data && response.data.updateProgram) {
          resolve(true);
        }
        else {
          reject("Please check your internet connection");
        }
      }, (error) => {
        // console.log(error);
        reject("Please check your internet connection");
      });
    });
  }
}
