import { Component, OnInit } from '@angular/core';
import { HomePageServicesService } from 'src/app/services/uiServices/home-page-services.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Constants } from 'src/app/Constants';
import { ServiceHandlerProvider } from 'src/app/services/service-handler.service';

@Component({
  selector: 'app-live-stream',
  templateUrl: './live-stream.component.html',
  styleUrls: ['./live-stream.component.css']
})
export class LiveStreamComponent implements OnInit {

  settings: Settings;
  liveUrl: any;
  viewsCount: string;
  constructor(private homeService: HomePageServicesService, private sanitizer: DomSanitizer, private serviceHandeler: ServiceHandlerProvider) { }

  ngOnInit() {
    this.getContactDetails();

  }
  async getContactDetails() {
    this.settings = await this.homeService.getSettings();
    // console.log(this.settings.liveUrl);
    let params = { "part": "statistics", "id": this.settings.liveUrl, "key": Constants.YOUTUBE_API_KEY };
    this.serviceHandeler.runService("https://www.googleapis.com/youtube/v3/videos", "GET", null, null, null, params).subscribe(response => {
      // console.log(response);
      this.viewsCount = response.items[0].statistics.viewCount;
    }, error => {
      // console.log(error);
    });
  }
}
