import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ArticlesService } from 'src/app/services/uiServices/articles.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Constants } from 'src/app/Constants';

@Component({
  selector: 'app-sports',
  templateUrl: './sports.component.html',
  styleUrls: ['./sports.component.css']
})
export class SportsComponent implements OnInit {
  articles: Array<Article>;
  articlesCount: number;
  constructor(private articlesService: ArticlesService, private sanitizer: DomSanitizer, ) {
    this.articlesCount = 5;
  }

  ngOnInit() {
    this.loadArticles();
  }
  async loadArticles() {
    this.articles = await this.articlesService.getArticlesList(true, this.articlesCount);
    // this.articles = this.articles.filter((article) => article.isShowen);
    this.articles = this.articles.map((article) => {
      let newArticle: any = article;
      newArticle.article = this.sanitizer.bypassSecurityTrustHtml(article.article);
      return newArticle;
    });
  }
  async loadMore() {
    this.articlesCount = this.articlesCount + 5;
    this.loadArticles();
  }
  getSharingLink(articleId: string): string {
    return Constants.SHARING_URL + "/articleDetails/sports/" + articleId;
  }
}
